
.AppHeader {
    display: block;
    height: 170px;
    width: 100%;
    background-color: var(--app-highlight);
    margin: 0px auto;
    padding: 25px 0;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }

#AppLogo {
  height: 100%;
  width: 40%;
  min-width: 200px;
  -webkit-animation: bounce-top 0.9s both;
  animation: bounce-top 0.9s both;
}

