.App {
  text-align: center;
  margin: 70px auto;
  width: 60vw;
  display: flex;
  flex-direction: column;
  background-color: rgb(215, 217, 219);
  background-color: var(--app-body-color-light);
  //box-shadow: 0px 6px 5px #413e3e;
  box-shadow: 0px 2px 4px  rgb(68, 68, 68);
  box-shadow: 0px 2px 4px  var(--box-shadow);
  border: 5px solid rgb(97, 221, 255);
  border: 5px solid var(--app-highlight);
  border-radius: 12px;
}

textarea:focus, input:focus{
  outline: none;
}

