.ThemeToggler-Wrapper {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 10vw;
    position: absolute;
    top: 70px;
    right: 35px;
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
    border-radius: 5px;
    border: 2px solid var(--app-highlight);
    background-color: var(--app-body-color);
    padding: 20px;
}

#themePickerVisibility {
    display: none ;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 90%;
    height: auto;
    -webkit-transition: 0.9s ease-in-out all;
    transition: 0.9s ease-in-out all;    
}

#theme-text {
    display: block;
    width: 100%;
    height: auto;
    font-family: 'Roboto', sans-serif;
    font-size: 1.3rem;
    font-weight: 500;
    line-height: 1.8rem;
    color: var(--app-highlight);
}

.themeChevron{
    display: block;
    font-size: 2rem;
    height: 5px;
    width: 100%;
    color: red;
    margin: 15px auto 0 auto;
    cursor: pointer;
}

.themeChevron:active {
    outline: 0;
    border: 0;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    /* hides mobile click banded */
}

.dropdown:hover .dropdown-content {
    display: block;
}

.ThemeToggler_btn {
    margin: 4px;
    width: 3rem;
    height: 3rem;
    border-radius: 5px; 
    border: none;
    box-shadow: 0 2px 2px var(--item-text);
    cursor: pointer;
}

