.CupboardDisplayItems-Wrapper--paddingWrapper {
  width: 100%;
  background-color: var(--app-body-color-light);
  margin: auto;
}

.CupboardDisplayItems-Wrapper {
  width: 93%;
  padding: 10px 0px;
  min-height: 120px;
  margin: 20px auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  background-color: var(--app-body-color-light);
  justify-content: center;
}

.CupboardItem {
  flex: 1 1 auto;
  display: grid;
  grid-template-columns: auto 60px;
  grid-template-rows: auto 60px;
  height: auto;
  max-height: 300px;
  width: 29%;
  max-width: 330px;
  overflow: hidden;
  margin: 4px;
  background-color: var(--item-bg);
  border: 3px solid var(--app-highlight); 
  border-radius: 5px;
  box-shadow: 0 2px 2px var(--box-shadow);
  -webkit-animation: scale-in-center 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  animation: scale-in-center 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

.CupboardItem button {
  background-color: var(--bttn-remove-item);
  color: white;
  grid-column: 2;
  grid-row: 2/3;
  justify-self: end;
  width: 100%;
  height: 100%;
  padding: 0;
  border: none;
  border-bottom-right-radius: 2px;
  text-align: center;
  vertical-align: middle;
  font-size: 1.6rem;
  line-height: auto;
  font-weight: 600;
  cursor: pointer;
}

.CupboardItem button:hover {
  background-color: var(--bttn-remove-item-toggled);
}

.CupboardItem h1 {
  grid-column: 1;
  grid-row: 2/2;
  color: var(--item-text);
  margin: auto;
  text-align: center;
  vertical-align: middle;
  display: inline-block;
  height: auto;
  width: 100%;
  font-family: 'Roboto', sans-serif;
